@import '../../../styles/variables.scss';
@import '../../../styles/breakpoints.scss';

.menu {
    &__back {
        display: block;
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(9, 17, 24, 0.5);
        z-index: 100;
        @include for-small-up {
            display: none;
        }
    }
    &__body {
        position: absolute;
        height: 100%;
        width: 40%;
        right: 0;
        background-color: #476785;
        top: 0;
        padding: 30px 60px 0 0;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        z-index: 101;
        @include for-large-up {
            width: 60%;
        }
        @include for-small-up {
            width: 100%;
        }
        &_close {
            svg {
                cursor: pointer;
            }
        }
        &_list {
            flex: 1;
            div {
                display: flex;
                flex-direction: column;
                justify-content: start;
                align-items: flex-end;
                height: 100%;
                a {
                    margin: 25px 0;
                    font-weight: 400;
                    font-size: 42px;
                    line-height: 54px;
                    color:  #BDCDDB;
                    &:hover {
                        color: #FCFCFC;
                    }
                }
            }
        }
    }
}